
import React from 'react';
import { Spring } from 'react-spring/renderprops';
import '../home.styles/MainPageStyle.css';
import lipsmodel from '../assets/lipsmodel.png';
import { Link } from 'react-router-dom';
import LipsProducts from './LipsProducts';
import { Helmet } from 'react-helmet';


class MainPageLips extends React.Component {
    constructor() {
        super();
        this.state = {
            country: 'NEWYORK'
        }
    }

    countrychange = () => {
        this.setState({
            country: 'INDIA'
        })
    }

    render() {
        return (
            <Spring
                from={{ opacity: 0 }}
                to={{ opacity: 1 }}
                config={{ delay: 1000, duration: 1000 }}
            >
                {props => (
                    <div style={props}>
                        <div class='mainPage'>
                            <Helmet>
                                <title>Lips</title>
                                <meta name="description" content="Maybelline Newyork Remake by UK Grafix" />
                            </Helmet>
                            <header>
                                <ul>
                                    <li><Link to='/'>HOME</Link></li>
                                    <li><Link to='/nails'>NAILS</Link></li>
                                    <li class='active'><Link to='/'>LIPS</Link></li>
                                    <li><Link to='/'>HOT PRODUCTS</Link></li>
                                    <li><Link to='/'>MY CART</Link></li>
                                </ul>
                            </header>
                            <Spring
                                from={{ opacity: 0 }}
                                to={{ opacity: 1 }}
                            >
                                {props => (
                                    <div style={props}>
                                        <body>
                                            <div>
                                                <img class='model' src={lipsmodel} alt="model" />
                                                <h2 class='behindtext'>MAYBELLINE</h2>
                                                <h3 onClick={this.countrychange} class='behindtext2'>{this.state.country}</h3>
                                            </div>
                                            <div>
                                                <LipsProducts />
                                            </div>
                                        </body>
                                    </div>
                                )}
                            </Spring>
                        </div>
                    </div>
                )}
            </Spring>
        );
    }
}

export default MainPageLips;