import React from 'react';
import { Spring } from 'react-spring/renderprops';
import '../home.styles/PostsStyle.css';
import nail1 from '../assets/nail1.png';
import nail2 from '../assets/nail2.png';
import nail3 from '../assets/nail3.png';
import heartempty from '../assets/heartempty.png';
import babylips from '../assets/babylips.png';
import ukgrafix from '../assets/ukgrafix.png';


class Posts extends React.Component {

    constructor() {
        super();
        this.state = {
            designerTxt: 'Crafted by UK Grafix',
            developer: 'UK Grafix'
        }
    }

    craftedTxt = () => {
        this.setState({
            designerTxt: 'Crafted in India by'
        })
    }

    render() {
        return (
            <Spring
                from={{ opacity: 0 }}
                to={{ opacity: 1 }}
                config={{ delay: 1000, duration: 1000 }}
            >
                {props => (
                    <div style={props}>
                        <div>
                            <h2 class='postsheader'>NEW ARRIVALS</h2>
                            <div class='postback'>
                                <h3 class='posttitles'>Velvet Red</h3>
                                <img class='heartimg' src={heartempty} alt='heart' />
                                <img class='nailimg' src={nail1} alt='nail1' />
                            </div>
                            <div class='postback1'>
                                <h3 class='posttitles'>Pure Purple</h3>
                                <img class='heartimg' src={heartempty} alt='heart' />
                                <img class='nail2img' src={nail3} alt='nail3' />
                            </div>
                            <div class='postback2'>
                                <h3 class='posttitles'>Baby Pink</h3>
                                <img class='heart1img' src={heartempty} alt='heart' />
                                <img class='nail1img' src={nail2} alt='nail2' />
                            </div>
                            <div class='postback3'>
                                <h3 class='posttitles'>Baby Lips</h3>
                                <img class='heart2img' src={heartempty} alt='' />
                                <img class='lipsimg' src={babylips} alt='babylips' />
                            </div>
                        </div>
                        <div>
                            <h3 /*onclick={this.craftedTxt}*/ class="ukgrafixtxt">Crafted by UK Grafix</h3>
                            <img class="ukgrafixlogo" src={ukgrafix} alt='ukgrafixlogo' />
                        </div>
                    </div>
                )}
            </Spring>

        );
    }
}

export default Posts;

