import React from 'react';
import '../home.styles/PageStyle.css';
import eyeshades from '../assets/eyeshades.jpeg';
import nails from '../assets/nails.jpg';
import lips from '../assets/lips.jpg';
import offertag from '../assets/offertag.png';
import { Link } from 'react-router-dom';

class PageChooser extends React.Component {

    render() {
        return (
            <div class='container'>
                <div class='container1'>
                    <h3>EYES</h3>
                    <Link to='/'><img class="eyeshade" src={eyeshades} alt="eyeshadesimg" /></Link>
                    <div class="offertag">
                        <img src={offertag} alt='offertag' />
                    </div>
                    <div class="pricetag"><h3>OFFER - 70%  <s>65%</s></h3></div>
                </div>
                <div class='container2'>
                    <h3>NAILS</h3>
                    <Link to='/nails'><img class='nails' src={nails} alt="nailsimg" /></Link>
                    <div class="offertag">
                        <img src={offertag} alt='offertag' />
                    </div>
                    <div class="pricetag"><h3>OFFER - 60%  <s>55%</s></h3></div>
                </div>
                <div class='container3'>
                    <h3>LIPS</h3>
                    <Link to='/lips'><img class='lips' src={lips} alt="lipsimg" /></Link>
                    <div class="offertag">
                        <img src={offertag} alt='offertag' />
                    </div>
                    <div class="pricetag"><h3>OFFER - 43%  <s>30%</s></h3></div>
                </div>
            </div>
        );
    }
}

export default PageChooser;