import React from 'react';
import './App.css';
import MainPage from './home.components/MainPage';
import MainPageNails from './nails.components/MainPageNails';
import PageNotFound from './PageNotFound';
import MainPageLips from './lips.components/MainPageLips';
import { Route, HashRouter as Router, Switch } from 'react-router-dom';

function App() {
  return (
    <Router>
      <Switch>
        <div class='main'>
          <Route exact path='/' component={MainPage} />
          <Route exact path='/nails' component={MainPageNails} />
          <Route exact path='/404' component={PageNotFound} />
          <Route exact path='/lips' component={MainPageLips} />
        </div>
      </Switch>
    </Router>
    /*
    <div class='main'>
      <div>
        <MainPage />
      </div>
    </div>
    */
  );
}

export default App;
