
import React from 'react';
import '../nails.styles/NailsProductStyle.css';
import nail1 from '../assets/nail1.png';
import nail2 from '../assets/nail2.png';
import nail3 from '../assets/nail3.png';
import { Button } from 'react-bootstrap';
import colorpallet1 from '../assets/colorpallet1.png';
import colorpallet2 from '../assets/colorpallet2.png';
import colorpallet3 from '../assets/colorpallet3.png';
import { Spring } from 'react-spring/renderprops';


class NailsProducts extends React.Component {
    render() {
        return (
            <div>
                <div class='blackspace' />
                <div>
                    <img class='nailimg1' src={nail1} alt='nail1' />
                    <h3 class='producttitle1'>SUPERSTAY CITY NUDES</h3>
                    <h4 class='productprice'>$30 USD</h4>
                    <h5 class='productdesc1'>Available in 5 Shades</h5>
                    <img class='pallet1' src={colorpallet1} alt='colorpallet1' />
                    <button type='button' class='button1'>ADD TO CART</button>
                </div>
                <div>
                    <img class='nailimg3' src={nail3} alt='nail3' />
                    <h3 class='producttitle2'>SUPERSTAY GEL NAIL COLOR</h3>
                    <h4 class='productprice2' >$35 USD</h4>
                    <h5 class='shadowtxt2' >Available in 5 Shades</h5>
                    <img class='colorp2' src={colorpallet2} alt='colorpallet2' />
                    <button type='button' class='button2'>ADD TO CART</button>
                </div>
                <div>
                    <img class='nailimg2' src={nail2} alt='nail2' />
                    <h3 class="producttitleimg2">SUPERSTAY 7 DAY COLOR</h3>
                    <h4 class="productprice3">$35 USD</h4>
                    <h5 class='productdesc3'>Available in 5 Shades</h5>
                    <img class='colorp1img2' src={colorpallet3} alt='colorpallete3' />
                    <button type='button' class='button3'>ADD TO CART</button>
                </div>
            </div>
        );
    }
}

export default NailsProducts;