
import React from 'react';
import lips1 from '../assets/lips1.png';
import lips2 from '../assets/lips2.png';
import lips3 from '../assets/lips3.png';
import '../lips.styles/lipsproductStyle.css';
import colorpallet1 from '../assets/colorpallet1.png';
import colorpallet2 from '../assets/colorpallet2.png';
import colorpallet3 from '../assets/colorpallet3.png';


class LipsProducts extends React.Component {
    render() {
        return (
            <div>
                <div class='blackspace' />
                <h2 class='blurproverrb' >The most Beautiful makeup of a women's passive.<br /> But cosmetics are easier to buy</h2>
                <div>
                    <img class='lips1img' src={lips1} alt='lipsimg1' />
                    <h3 class='producttitle'>COLOR SENSATIONAL LIPSTICK</h3>
                    <h4 class='productdesc'>$45USD</h4>
                    <button class="buttonpos">ADD TO CART</button>
                    <h5 class='productdesc2'>Available in 5 Shades</h5>
                    <img class='colorp1' src={colorpallet1} alt='colorpallet1' />
                </div>
                <div>
                    <img class='lips2img' src={lips2} alt='lipsimg2' />
                    <h3 class='producttitleimg3'>PURPLE ALERT LIPSTICK</h3>
                    <h4 class='productdescimg3'>$30USD</h4>
                    <button class="buttonposimg3">ADD TO CART</button>
                    <h5 class='productdesc2img3'>Available in 5 Shades</h5>
                    <img class='colorp1img3' src={colorpallet2} alt='colorpallet2' />
                </div>
                <div>
                    <img class='lips3img' src={lips3} alt='lipsimg3' />
                    <h3 class="producttitleimg2">REBEL BOUQUET LIPSTICK</h3>
                    <h4 class="productdescimg2">$40USD</h4>
                    <button class='buttonposimg2'>ADD TO CART</button>
                    <h5 class='productdesc2img2'>Available in 5 Shades</h5>
                    <img class='colorp1img2' src={colorpallet3} alt='colorpallete3' />
                </div>
            </div>
        );
    }
}

export default LipsProducts;