import React from 'react';
import modelimage from '../assets/modelimage.png';
import { Spring } from 'react-spring/renderprops';
import '../nails.styles/MainPageStylesNails.css';
import { Link } from 'react-router-dom';
import NailsProducts from './NailsProducts';
import { Helmet } from 'react-helmet';


class MainPageNails extends React.Component {

    constructor() {
        super();
        this.state = {
            country: 'NEWYORK'
        }
    }

    countrychange = () => {
        this.setState({
            country: 'INDIA'
        })
    }

    render() {
        return (
            <Spring
                from={{ opacity: 0 }}
                to={{ opacity: 1 }}
                config={{ delay: 1000, duration: 1000 }}
            >
                {props => (
                    <div style={props}>
                        <div class='mainPage'>
                            <header>
                                <Helmet>
                                    <title>Nails</title>
                                    <meta name="description" content="Maybelline Newyork Remake by UK Grafix" />
                                </Helmet>
                                <ul>
                                    <li><Link to='/'>HOME</Link></li>
                                    <li class='active'><Link>NAILS</Link></li>
                                    <li><Link to='/lips'>LIPS</Link></li>
                                    <li><Link to='/'>HOT PRODUCTS</Link></li>
                                    <li><Link to='/'>MY CART</Link></li>
                                </ul>
                            </header>
                            <Spring
                                from={{ opacity: 0 }}
                                to={{ opacity: 1 }}
                                config={{ delay: 2000, duration: 2000 }}
                            >
                                {props => (
                                    <div style={props}>
                                        <body>
                                            <div>

                                            </div>
                                            <div>
                                                <img class='model' src={modelimage} alt="model" />
                                                <h2 class='behindtext'>MAYBELLINE</h2>
                                                <h3 onClick={this.countrychange} class='behindtext2'>{this.state.country}</h3>
                                                <h2 class='blurproverb'>Beauty and worth aren't found in a Makeup Bottle</h2>
                                            </div>
                                            <Spring
                                                from={{ opacity: 0 }}
                                                to={{ opacity: 1 }}
                                                config={{ delay: 2500, duration: 2500 }}
                                            >
                                                {props => (
                                                    <div>
                                                        <NailsProducts />
                                                    </div>
                                                )}
                                            </Spring>
                                        </body>
                                    </div>
                                )}
                            </Spring>
                        </div>
                    </div>
                )}
            </Spring>
        );
    }

}

export default MainPageNails;